<template>
  <el-form
  ref="additionalRef"
  label-position="top"
  :rules="rules"
  :model="additional"
  label-width="120px">

  <div class="form_container">
    <el-row class="row-bg" justify="space-between">
      <el-col :span="6">
        <el-form-item label="Размер футболки" prop="size">
        <el-select v-model="additional.size" placeholder="Размер">
          <el-option
            v-for="item in additional.sizeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      </el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6"></el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between">
      <el-col :span="15">
        <el-form-item>
          <el-col :span="12">
            <p class="other_label">
              Есть ли аллергия на продукты питания и/или медицинские препараты?
            </p>
            </el-col>
          <el-col :span="2">
        <el-switch v-model="additional.is_allergy" />
          </el-col>
      </el-form-item>
      </el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between" v-if="additional.is_allergy">
      <el-col :span="15">
        <el-form-item label="Укажите на какие именно продукты и медицинские препараты есть аллергия:" prop="note_allergy">
          <el-input v-model="additional.note_allergy" type="textarea"></el-input>
      </el-form-item>
      </el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between">
      <el-col :span="15">
        <el-form-item>
          <el-col :span="12">
            <p class="other_label">
              Относится ли участник к категории обучающихся с ОВЗ и/или инвалидностью?
            </p>
            </el-col>
          <el-col :span="2">
        <el-switch v-model="additional.is_voz" />
          </el-col>
      </el-form-item>
      </el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between">
      <el-col :span="15">
        <el-form-item>
          <el-col :span="12">
            <p class="other_label">
              Нуждается ли участник в специальных условиях при организации олимпиады?
            </p>
            </el-col>
          <el-col :span="2">
        <el-switch v-model="additional.is_need_conditions" />
          </el-col>
      </el-form-item>
      </el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between" v-if="additional.is_need_conditions">
      <el-col :span="15">
        <el-form-item label="Укажите какие именно условия необходимо предоставить:" prop="note_conditions">
          <el-input v-model="additional.note_conditions" type="textarea"></el-input>
      </el-form-item>
      </el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between">
      <el-col :span="15">
        <el-form-item label="Особые сведения (непереносимость медицинских препаратов, хронический заболевания, о которых необходимо знать организаторам)">
          <el-input v-model="additional.note_special" type="textarea"></el-input>
      </el-form-item>
      </el-col>
    </el-row>
  </div>


  <div class="form_container">
    <span class="download_link" @click="downloadFile">
      Скачать шаблон
      <el-icon><download /></el-icon>
      </span>
    <el-form-item label= "Загрузите файл согласия на передачу и обработку персональных данных участника" prop="photo">
    <el-upload
      ref="upload"
      drag
      action="#"
      :auto-upload="false"
      :on-change="handleChange"
      :limit="1"
      :on-exceed="handleExceed"
    >

    <el-icon class="el-icon--upload"><upload-filled /></el-icon>
    <div class="el-upload__text">
      Перетащите сюда файл или <em>кликните</em>
    </div>
    <template #tip>
      <div class="el-upload__tip">
        jpg/pdf файл не более 10мб
      </div>
    </template>
  </el-upload>
    </el-form-item>
  </div>

  <div class="btns">
    <el-form-item>
      <el-button type="primary" @click="onSubmit(this.$refs.additionalRef)"
        >Далее</el-button
      >
    </el-form-item>
    <el-button
    class="btns_prev"
    type="primary"
    plain
    @click="$emit('prevStep')"
        >Назад</el-button
      >
  </div>
  </el-form>
</template>

<script>
import { UploadFilled, Download } from '@element-plus/icons-vue'
import { jsPDF } from "jspdf"
import { mapActions } from 'vuex'

export default {
  emits: ['nextStep', 'prevStep'],
  components: {
    UploadFilled,
    Download
  },
  data() {
    return {
      additional: {
        size: "",
        is_allergy: 0,
        note_allergy: "",
        is_voz: 0,
        is_need_conditions: 0,
        note_conditions: "",
        note_special: "",
        agree_file: '',
        sizeOptions: [
          {
            label: 'S',
            value: 'S'
          },
          {
            label: 'M',
            value: 'M'
          },
          {
            label: 'L',
            value: 'L'
          },
          {
            label: 'XL',
            value: 'XL'
          },
        ]
    },
    rules: {
      size: [
        {
            required: true,
            message: 'Выберите размер',
            trigger: 'change',
          }
      ],
      note_allergy: [
          {
            validator: this.allergyVal,
            trigger: 'blur',
          }
        ],
        note_conditions: [
          {
            validator: this.conditionsVal,
            trigger: 'blur',
          }
        ],
        photo: [
          {
            validator: this.validatePhoto,
            trigger: 'blur'
          }
        ]
    }
    }
  },

  methods: {
    ...mapActions(['SET_ADDITIONAL']),
    allergyVal(rule, value, callback) {
      if (value === '' && this.additional.is_allergy) {
        callback(new Error('Укажите данные'))
      } else {
          callback()
        }
    },
    conditionsVal(rule, value, callback) {
      if (value === '' && this.additional.is_need_conditions) {
        callback(new Error('Укажите данные'))
      } else {
          callback()
        }
    },
    downloadFile() {
      const doc = new jsPDF();
      doc.text("Hello world!", 15, 15);
      doc.save('output.pdf')
    },
    validatePhoto(rule, value, callback) {
      const bytes = this.additional.agree_file.size;
      const type = this.additional.agree_file.type;
      if (!bytes) {
        callback(new Error('Загрузите файл'))
      }
      else if (!type.includes('jpeg') && !type.includes('jpg') && !type.includes('pdf')) {
        callback(new Error('Файл должен быть в формате jpg или pdf'))
      }
      else if (bytes / Math.pow(1000, 2) > 10) {
          callback(new Error('Файл должен быть не более 10мб'))
        } else {
          callback()
        }
    },
    handleExceed(files) {
      this.$refs.upload.clearFiles();
      this.$refs.upload.handleStart(files[0]);
    },
    handleChange(file) {
      this.additional.agree_file = '';
      this.additional.agree_file = file.raw;
    },
    onSubmit(formEl) {
      const data = {
        additional: {
          size: this.additional.size,
          is_allergy: this.additional.is_allergy ? "1" : "0",
          note_allergy: this.additional.note_allergy,
          is_voz: this.additional.is_voz ? "1" : "0",
          is_need_conditions: this.additional.is_need_conditions ? "1" : "0",
          note_conditions: this.additional.note_conditions,
          note_special: this.additional.note_special
        },
        agree_file: this.additional.agree_file,
      }
      if (!formEl && (this.additional.is_need_conditions || this.additional.is_allergy)) return
       formEl.validate((valid) => {
      if (valid) {
        this.SET_ADDITIONAL(data)
        this.$emit('nextStep');
      } else {
        return false
    }
  })
    }
  }

}
</script>

<style>

</style>