<template>

<el-form
  ref="routelRef"
  label-position="top"
  :rules="rules"
  :model="route"
  label-width="120px"
  v-loading="isLoadingPostReq">

  <div class="form_container">
    <h3 class="form_subtitle" style="marginBottom: 0">Для участников из регионов РФ, кроме г. Москвы</h3>
  </div>

  <div class="form_container">
    <h3 class="form_subtitle">Прилет/приезд в г. Москва</h3>
    <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Дата и время прибытия (по московскому времени)" prop="date_arrival">
        <el-date-picker
        v-model="route.date_arrival"
        type="datetime"
        placeholder="гггг-мм-дд чч:мм:сс"
        value-format="YYYY-MM-DD HH:mm:ss"
      >
      </el-date-picker>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Вид транспорта" prop="type_transport_arrival">
        <el-select v-model="route.type_transport_arrival" placeholder="Вид транспорта">
          <el-option
            v-for="item in typeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Место прибытия (аэропорт/ ж/д вокзал, автовокзал и т.д.)" prop="place_arrival">
        <el-input
        v-model="route.place_arrival"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Номер рейса самолета или номер поезда и вагона" prop="number_arrival">
        <el-input
        v-model="route.number_arrival"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>
  </div>

  <div class="form_container">
    <h3 class="form_subtitle">Вылет/выезд из г. Москва</h3>
    <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Дата и время отъезда (по московскому времени)" prop="date_departure">
        <el-date-picker
        v-model="route.date_departure"
        type="datetime"
        placeholder="гггг-мм-дд чч:мм:сс"
        value-format="YYYY-MM-DD HH:mm:ss"
      >
      </el-date-picker>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Вид транспорта" prop="type_transport_departure">
        <el-select v-model="route.type_transport_departure" placeholder="Вид транспорта">
          <el-option
            v-for="item in typeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Место отбытия (аэропорт/ ж/д вокзал, автовокзал и т.д.)" prop="place_departure">
        <el-input
        v-model="route.place_departure"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Номер рейса самолета или номер поезда и вагона" prop="number_departure">
        <el-input
        v-model="route.number_departure"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  </div>


<div class="btns">
    <el-form-item>
      <el-button type="primary" @click="onSubmit(this.$refs.routelRef)"
        >Завершить регистрацию</el-button
      >
    </el-form-item>
    <el-button
    class="btns_prev"
    type="primary"
    plain
    @click="$emit('prevStep')"
        >Назад</el-button
      >
  </div>
  </el-form>

</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { ElNotification} from 'element-plus'
export default {
  emits: ['nextStep', 'prevStep'],
  data() {
    return {
      route: {
        "date_arrival": "",
        "type_transport_arrival": '',
        "place_arrival": "",
        "number_arrival": "",
        "date_departure": "",
        "type_transport_departure": '',
        "place_departure": "",
        "number_departure": ""
      },
      typeOptions: [
        {
          label: 'Автобус',
          value: '1'
        },
        {
          label: 'Поезд',
          value: '2'
        },
        {
          label: 'Самолет',
          value: '3'
        },
        {
          label: 'Автомобиль',
          value: '4'
        },
      ],
      rules: {
        date_arrival: [
          {
            type: 'date',
            required: true,
            message: 'Укажите дату',
            trigger: 'change',
          },
        ],
        date_departure: [
          {
            type: 'date',
            required: true,
            message: 'Укажите дату',
            trigger: 'change',
          },
        ],
        type_transport_arrival: [
          {
            required: true,
            message: 'Выберите вид транспорта',
            trigger: 'change',
          }
        ],
        type_transport_departure: [
          {
            required: true,
            message: 'Выберите вид транспорта',
            trigger: 'change',
          }
        ],
        place_arrival: [
          {
            required: true,
            message: 'Введите данные',
            trigger: 'blur',
          }
        ],
        place_departure: [
          {
            required: true,
            message: 'Введите данные',
            trigger: 'blur',
          }
        ],
        number_arrival: [
          {
            required: true,
            message: 'Введите данные',
            trigger: 'blur',
          }
        ],
        number_departure: [
          {
            required: true,
            message: 'Введите данные',
            trigger: 'blur',
          }
        ],
      }
    }
  },
  computed: {
    ...mapGetters(['isLoadingPostReq'])
  },
  methods: {
    ...mapActions(['SET_ROUTE', 'POST_REGISTER', 'POST_FILES']),
    onSubmit(formEl) {
      // this.$emit('nextStep');
      if (!formEl) return
       formEl.validate((valid) => {
      if (valid) {
        this.SET_ROUTE(this.route);
        this.POST_REGISTER()
        .then((res) => {
          if(res.data.error_message) {
            const errors = JSON.parse(res.data.error_message);
            for (let error in errors) {
              ElNotification({
              title: 'Ошибка',
              message: `${errors[error]}`,
              type: 'error',
            })
            }
          } else {
            this.POST_FILES(res.data.hash).then(() => {
              ElNotification({
              title: 'Готово!',
              message: `Подтвердите почту`,
              type: 'success',
            })
            this.$emit('nextStep');
            })
          }
        })
      } else {
        return false
    }
  })
    }
  }

}
</script>
