import { createStore } from 'vuex';
import axios from 'axios';

export default createStore({
  state: {
    contestant: {
      user: {
        surname: "",
        name: "",
        patronymic: "",
        sex: "",
        phone: "",
        birthday: "",
        email: "",
        password: ""
      },
      document: {
        type: "",
        series: "",
        number: "",
        date_issue: "",
        authority: ""
      },
      address: {
        region: "",
        zone: "",
        city: ""
      },
      school: {
        full_name: "",
        short_name: "",
        status_olympic: "",
        mark_olympic: "",
        grade_number: "",
        grade_letter: "",
        grade_performs: "",
        fio_teacher: "",
        place_work: "",
        post: "",
        academic_degree: ""
      },
      additional: {
        size: "",
        is_allergy: 0,
        note_allergy: "",
        is_voz: 0,
        is_need_conditions: 0,
        note_conditions: "",
        note_special: ""
      },
      accompanying_persons: {
        list: []
      },
      route: {
        date_arrival: "",
        type_transport_arrival: '',
        place_arrival: "",
        number_arrival: "",
        date_departure: "",
        type_transport_departure: '',
        place_departure: "",
        number_departure: ""
      },
    },
    photo: '',
    agree_file: '',
    accompanying: {
      fio: "",
      sex: "",
      birthday: "",
      place_birth: "",
      phone: "",
      email: "",
      place_work: "",
      post: "",
      agree_file: ''
    },
    gerionsList: [],
    accList: [],
    isLoadingPostReq: false,
    isLoadingBlock: false,
    checkURL: 'https://api.sdotest.mpgu.org/literature/'
  },
  mutations: {
    SET_REGIONS_TO_STATE (state, regions) {
      state.gerionsList = regions;
    },
    SET_ACCLIST_TO_STATE (state, list) {
      state.accList = list;
    },
    SET_USER (state, user) {
      state.contestant.user = user;
    },
    SET_DOCUMENT (state, document) {
      state.contestant.document = document;
    },
    SET_ADDRESS (state, address) {
      state.contestant.address = address;
    },
    SET_PHOTO (state, photo) {
      state.photo = photo;
    },
    SET_SCHOOL_TO_STATE (state, school) {
      state.contestant.school = school
    },
    SET_ADDITIONAL_TO_STATE (state, data) {
      state.contestant.additional = data
    },
    SET_AGREEFILE_TO_STATE (state, data) {
      state.agree_file = data
    },
    SET_ACCOMP_PEOPLE_TO_STATE (state, list) {
      state.contestant.accompanying_persons.list = list
    },
    SET_ACC_PER (state, person) {
      state.accompanying = person.accompanying
    },
    SET_ROUTE_TO_STATE (state, data) {
      state.route = data
    },
  },
  actions: {
    GET_PHONE ({state},  phone) {

      return axios(`${state.checkURL}exists-phone`, {
        method: 'POST',
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
        },
        data: {
          phone: phone
        }
      })
      .then((res) => {
        return res;
      })
      .catch((error) => {
        return error;
      });
    },
    GET_EMAIL ({state}, email) {
      return axios(`${state.checkURL}exists-email`, {
        method: 'POST',
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
        },
        data: {
          email: email
        }
      })
      .then((res) => {
        return res;
      })
      .catch((error) => {
        return error;
      });
    },
    GET_REGIONS ({commit}) {
      this.state.isLoadingBlock = true
      return axios('https://api.sdotest.mpgu.org/literature/regions' , {
        method: 'GET',
        // headers: {
        //   'X-Requested-With': 'XMLHttpRequest',
        //   'Access-Control-Allow-Origin': '*',
        // },
      })
      .then((regions) => {
        commit('SET_REGIONS_TO_STATE', regions.data)
        this.state.isLoadingBlock = false
        return regions;
      })
      .catch((error) => {
        return error;
      });
    },
    GET_ACC_PER ({commit, state}) {
      state.isLoadingBlock = true;
      return axios('https://api.sdotest.mpgu.org/literature/persons' , {
        method: 'GET',
      })
      .then((list) => {
        commit('SET_ACCLIST_TO_STATE', list.data.data)
        state.isLoadingBlock = false
        return list;
      })
      .catch((error) => {
        return error;
      });
    },
    POST_ACC_PER ({state}, data) {
      state.isLoadingPostReq = true;
      return axios.post('https://api.sdotest.mpgu.org/literature/add-personal', data)
      .then((res) => {
        state.isLoadingPostReq = false;
        return res;
      })
      .catch((error) => {
        state.isLoadingPostReq = false;
        return error;
      });
    },
    POST_REGISTER({state}) {
      state.isLoadingPostReq = true
      return axios.post('https://api.sdotest.mpgu.org/literature/register', state.contestant)
      .then((res) => {
        state.isLoadingPostReq = false
        return res;
      })
      .catch((error) => {
        state.isLoadingPostReq = false;
        return error;
      });
    },
    POST_FILES({state }, hash) {
      state.isLoadingPostReq = true
      const formData = new FormData();
      formData.append('photo', state.photo);
      formData.append('agree_file', state.agree_file);

      return axios.post(`https://api.sdotest.mpgu.org/literature/send-file?hash=${hash}`, formData)
      .then((res) => {
        state.isLoadingPostReq = false
        return res;
      })
      .catch((error) => {
        state.isLoadingPostReq = false
        return error;
      });
    },
    SET_PESONAL_DATA ({commit}, data) {
      commit('SET_USER', data.user);
      commit('SET_DOCUMENT', data.document);
      commit('SET_ADDRESS', data.address);
      commit('SET_PHOTO', data.photo);
    },
    SET_SCHOOL ({commit}, data) {
      commit('SET_SCHOOL_TO_STATE', data.school)
    },
    SET_ADDITIONAL ({commit}, data) {
      commit('SET_ADDITIONAL_TO_STATE', data.additional);
      commit('SET_AGREEFILE_TO_STATE', data.agree_file);
    },
    SET_ACCOMP_PEOPLE ({commit}, data) {
      commit('SET_ACCOMP_PEOPLE_TO_STATE', data.list);
    },
    SET_ROUTE ({commit}, data) {
      commit('SET_ROUTE_TO_STATE', data)
    },
  },
  getters: {
    isLoadingPostReq(state) {
      return state.isLoadingPostReq
    },
    isLoadingBlock(state) {
      return state.isLoadingBlock
    },
    accList(state) {
      return state.accList
    },
    regions(state) {
      return state.gerionsList
    },
    isMoscow(state) {
      const region = state.contestant.address.region;
      const city = state.contestant.address.city.toLowerCase();
      return region === "77" || city.includes('москва');
    },
  }
})