<template>
  <el-form
  ref="userRef"
  label-position="top"
  :rules="rules"
  :model="user"
  label-width="120px">
  <div class="form_container">
  <el-row class="row-bg" justify="space-between">
    <el-col :span="6">
      <el-form-item label="Фамилия" prop="surname">
        <el-input
        v-model="user.surname"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Имя" prop="name">
        <el-input
        v-model="user.name"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Отчество" prop="patronymic">
      <el-input
        class="w-50 m-2"
        v-model="user.patronymic"
        ></el-input>
      </el-form-item>
    </el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="6">
      <el-form-item label="Пол" prop="sex">
      <el-radio-group v-model="user.sex" >
        <el-radio label="Мужской"></el-radio>
        <el-radio label="Женский"></el-radio>
      </el-radio-group>
    </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Дата рождения" prop="birthday">
        <el-date-picker v-model="user.birthday" type="date" value-format="YYYY-MM-DD" placeholder="гггг-мм-дд" >
      </el-date-picker>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Пароль" prop="password">
      <el-input
        v-model="user.password"
        type="password"
        autocomplete="off"
        show-password

        ></el-input>
      </el-form-item>
    </el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="6">
      <el-form-item
      label="Телефон" prop="phone">
        <el-input
        v-model="user.phone"

        placeholder="+79000000000"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item
      label="Email"
      prop="email"
      >
        <el-input
        v-model="user.email"

        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Подтвердите пароль" prop="checkPass">
      <el-input
        v-model="user.checkPass"
        type="password"
        autocomplete="off"
        show-password

        ></el-input>
      </el-form-item>
    </el-col>
  </el-row>
  </div>

  <div class="form_container">
    <el-row class="row-bg" justify="space-between">
    <el-col :span="8">
      <el-form-item label="Документ, удостоверяющий личность" prop="type">
        <el-select v-model="user.type" placeholder="Тип документа"
        >
          <el-option
            v-for="item in user.documentOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
    </el-col>
    <el-col :span="5"></el-col>
    <el-col :span="5"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="6">
      <el-form-item label="Серия" prop="series">
        <el-input
        v-model="user.series"

        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Номер" prop="number">
      <el-input
        v-model="user.number"

        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Кем выдан" prop="authority">
        <el-input
        v-model="user.authority"

        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
  <el-col :span="6">
      <el-form-item label="Дата выдачи" prop="date_issue">
        <el-date-picker
        v-model="user.date_issue"
        type="date"
        placeholder="гггг-мм-дд"
        value-format="YYYY-MM-DD"
        >
      </el-date-picker>
      </el-form-item>
    </el-col>
  </el-row>
  </div>

  <div class="form_container">
    <el-row class="row-bg" justify="space-between">
      <el-col :span="6">
        <el-form-item label="Регион проживания" prop="region">
          <el-skeleton :loading="isLoadingBlock" animated>
          <template #template>
          <el-skeleton-item variant="h1" style="width: 70%; height: 30px"/>
          </template>
        </el-skeleton>
          <el-select
          v-if="!isLoadingBlock"
          filterable
          v-model="user.region"
          placeholder="Регион проживания"
          >
          <el-option
            v-for="(item, index) in regions"
            :key="index"
            :label="item"
            :value="index"
          >
          </el-option>
        </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6"></el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between">
      <el-col :span="6">
        <el-form-item label="Район/область" prop="zone">
          <el-input
          v-model="user.zone"

          ></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6"></el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between">
      <el-col :span="6">
        <el-form-item label="Город/населенный пункт" prop="city">
          <el-input
          v-model="user.city"

          ></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="6"></el-col>
      <el-col :span="6"></el-col>
    </el-row>
  </div>

  <div class="form_container">
    <el-form-item label="Загрузите фото (3x4)" prop="photo">
    <el-upload
      ref="upload"
      drag
      action="#"
      :auto-upload="false"
      :on-change="handleChange"
      :limit="1"
      :on-exceed="handleExceed"
    >
    <el-icon class="el-icon--upload"><upload-filled /></el-icon>
    <div class="el-upload__text">
      Перетащите сюда файл или <em>кликните</em>
    </div>
    <template #tip>
      <div class="el-upload__tip">
        jpg/pdf файл не более 10мб
      </div>
    </template>
  </el-upload>
    </el-form-item>
  </div>

  <div class="btns">
    <el-form-item>
      <el-button type="primary" @click="onSubmit(this.$refs.userRef)"
        >Далее</el-button
      >
    </el-form-item>
  </div>

  </el-form>

</template>

<script>
import { UploadFilled } from '@element-plus/icons-vue'
import { mapActions, mapGetters } from 'vuex'
import { ElNotification } from 'element-plus'
export default {
  emits: ['nextStep'],
  components: {
    UploadFilled
  },
  data() {
    return {
      user: {
        file: '',
        surname: '',
        name: '',
        patronymic: '',
        sex: '',
        birthday: '',
        password: '',
        checkPass: '',
        phone: '',
        email: '',
        type: '',
        documentOptions: [
          {
            label: 'Паспорт',
            value: '1'
          },
          {
            label: 'Cвидетельство о рождении',
            value: '2'
          }
        ],
        series: '',
        number: '',
        data_issue: '',
        authority: '',
        photo: '',
        region: '',
        zone: '',
        city: ''
      },
      rules: {
        surname: [
          {
            required: true,
            message: 'Введите фамилию',
            trigger: 'blur',
          }
        ],
        name: [
          {
            required: true,
            message: 'Введите имя',
            trigger: 'blur',
          }
        ],
        sex: [
          {
            required: true,
            message: 'Укажите пол',
            trigger: 'blur',
          }
        ],
        birthday: [
          {
            type: 'date',
            required: true,
            message: 'Укажите дату рождения',
            trigger: 'change',
          },
          {
            validator: this.validateBirthday,
            trigger: 'change'
          }
        ],
        password: [
          {
            required: true,
            message: 'Введите пароль',
            trigger: 'blur',
          },
          {
            validator: this.validatePass,
            trigger: 'blur',
          },
        ],
        checkPass: [
          {
            required: true,
            message: 'Повторите пароль',
            trigger: 'blur'
          },
          {
            validator: this.validatePass2,
            trigger: 'blur'
          }
        ],
        email: [
          {
            required: true,
            message: 'Введите email',
            trigger: 'blur',
          },
          {
            type: 'email',
            message: 'Email некорректный',
            trigger: 'blur',
          },
          {
            validator: this.validateEmail,
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,
            message: 'Введите номер телефона'
          },
          {
            validator: this.validatePhone,
            trigger: 'blur'
          }
        ],
        type: [
          {
            required: true,
            message: 'Выберите тип документа',
            trigger: 'change',
          }
        ],
        region: [
          {
            required: true,
            message: 'Выберите регион',
            trigger: 'change',
          }
        ],
        zone: [
          {
            required: true,
            message: 'Выберите район',
            trigger: 'change',
          }
        ],
        city: [
          {
            required: true,
            message: 'Выберите город',
            trigger: 'change',
          }
        ],
        series: [
          { required: true,
            message: 'Введите серию'
          },
        ],
        number: [
          { required: true,
            message: 'Введите номер'
          },
        ],
        authority: [
          {
            required: true,
            message: 'Укажите орган, выдавший документ',
            trigger: 'blur',
          }
        ],
        date_issue: [
          {
            type: 'date',
            required: true,
            message: 'Укажите дату выдачи',
            trigger: 'change',
          },
          {
            validator: this.validateBirthday,
            trigger: 'change'
          }
        ],
        photo: [
          {
            required: true,
            message: 'Загрузите фото',
            trigger: 'blur'
          },
          {
            validator: this.validatePhoto,
            trigger: 'blur'
          }
        ]
      }
    }
  },

  created() {
    this.GET_REGIONS();
    this.GET_EMAIL();

  },

  computed: {
    ...mapGetters(['regions', 'isLoadingBlock',]),
  },

  methods: {
    ...mapActions([
      'SET_PESONAL_DATA',
      'GET_REGIONS',
      'GET_EMAIL',
      'GET_PHONE']),
    validateEmail(rule, value, callback) {
      this.GET_EMAIL(value)
      .then((res) => {
        if (!res.data.success_message) {
          ElNotification({
              title: 'Ошибка',
              message: `${JSON.parse(res.data.error_message).email}`,
              type: 'error',
            })
          callback(new Error('Email уже существует'))
        } else {
          callback()
        }
      })
    },
    validatePhone(rule, value, callback) {
      this.GET_PHONE(value)
      .then((res) => {
        if (!res.data.success_message) {
          ElNotification({
              title: 'Ошибка',
              message: `${JSON.parse(res.data.error_message).phone}`,
              type: 'error',
            })
          callback(new Error('Телефон уже существует'))
        } else {
          callback()
        }
      })
    },
    validateBirthday(rule, value, callback) {
      const now = new Date();
      const birthday = new Date(value);
      if (now  < birthday || now.getDate() === birthday.getDate()) {
          callback(new Error('Некорректная дата'))
        } else {
          callback()
        }
    },
    validatePass(rule, value, callback){
      if (value.length  < 6) {
          callback(new Error('Должно быть минимум 6 символов'))
        } else {
          callback()
        }
    },
    validatePass2(rule, value, callback) {
      if (value === '') {
          callback(new Error('Подтвердите пароль'))
        } else if (value !== this.user.password) {
          callback(new Error("Пароли не совпадают"))
        } else {
          callback()
        }
    },
    validatePhoto(rule, value, callback) {
      const bytes = this.user.photo.size;
      const type = this.user.photo.type;
      if (!type.includes('jpeg') && !type.includes('jpg') && !type.includes('pdf')) {
        callback(new Error('Файл должен быть в формате jpg или pdf'))
      }
      else if (bytes / Math.pow(1000, 2) > 10) {
          callback(new Error('Файл должен быть не более 10мб'))
        } else {
          callback()
        }
    },
    handleExceed(files) {
      this.$refs.upload.clearFiles();
      this.$refs.upload.handleStart(files[0]);
    },
    handleChange(file) {
      this.user.photo = '';
      this.user.photo = file.raw;
    },
    onSubmit(formEl) {

      const data = {
        user: {
          surname: this.user.surname,
          name: this.user.name,
          patronymic: this.user.patronymic,
          sex: this.user.sex === 'Мужской' ? '1' : '2',
          phone: this.user.phone,
          birthday: this.user.birthday,
          email: this.user.email,
          password: this.user.password
        },
        document: {
          type: this.user.type,
          series: this.user.series,
          number: this.user.number,
          date_issue: this.user.date_issue,
          authority: this.user.authority
        },
        address: {
          region: this.user.region,
          zone: this.user.zone,
          city: this.user.city
        },
        photo: this.user.photo
      }
        if (!formEl) return
       formEl.validate((valid) => {
      if (valid) {
        this.SET_PESONAL_DATA(data);
        this.$emit('nextStep');
      } else {
        return false
    }
  })
    }

}
}
</script>