<template>
  <el-form
  ref="additionalRef"
  label-position="top"
  :rules="rules"
  :model="accPerson"
  label-width="120px"
  v-loading="isLoadingPostReq">

  <div class="form_container">
    <el-row class="row-bg" justify="space-between">
      <el-col :span="20">
        <el-form-item label="Выберите из списка сопровождающее лицо" prop="person">
          <el-table
          v-loading="isLoadingBlock"
          :data="filterTableData.slice(currentIndex, currentIndex + pageSize)" style="width: 100%" >


          <el-table-column label="ФИО" prop="fio" width="350" />

            <el-table-column label="Должность/родитель" prop="post" width="250"/>

          <el-table-column align="center">
            <template #header>
              <el-input
              v-model="searchFIO"
              size="small"
              placeholder="Введите ФИО"/>
            </template>
            <template #default="scope">
              <el-button
                v-if="accPerson.accompanying_persons.list.indexOf(scope.row.id) === -1"
                size="small"
                type="primary"
                @click="handleAdd(scope.$index, scope.row)"
                >Выбрать</el-button
              >
              <el-button
                v-if="accPerson.accompanying_persons.list.indexOf(scope.row.id) > -1"
                size="small"
                type="primary"
                plain
                @click="handleDelete(scope.$index, scope.row)"
                >Отменить выбор</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          small
          background
          layout="prev, pager, next"
          style="marginTop: 40px"
          :total="totalItems"
          v-model:currentPage="currentPage"
          v-model:page-size="pageSize">
          </el-pagination>
        </el-form-item>
      </el-col>
    </el-row>

    <el-row class="row-bg" justify="space-between" >
      <el-col :span="15">
        <el-form-item>
          <el-col :span="12">
            <p class="other_label">
              В списке нет сопровождающего лица?
            </p>
            </el-col>
          <el-col :span="2">
        <el-switch v-model="isAdd" />
          </el-col>
      </el-form-item>
      </el-col>
    </el-row>
  </div>

  <div class="form_container" v-if="isAdd">
    <h3 class="form_subtitle">Данные сопровождающего лица</h3>
    <el-form
  ref="additionalAddRef"
  label-position="top"
  :rules="rules"
  :model="accPersonAdd"
  label-width="120px">
  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Фамилия, имя, отчество" prop="fio">
        <el-input
        v-model="accPersonAdd.fio"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="6">
      <el-form-item label="Пол" prop="sex">
      <el-radio-group v-model="accPersonAdd.sex">
        <el-radio label="Мужской"></el-radio>
        <el-radio label="Женский"></el-radio>
      </el-radio-group>
    </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item label="Дата рождения" prop="birthday">
        <el-date-picker v-model="accPersonAdd.birthday" type="date" value-format="YYYY-MM-DD" placeholder="гггг-мм-дд">
      </el-date-picker>
      </el-form-item>
    </el-col>
    <el-col :span="6">
    </el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="6">
      <el-form-item
      label="Телефон" prop="phone">
        <el-input
        v-model="accPersonAdd.phone"
        placeholder="+79000000000"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
      <el-form-item
      label="Email"
      prop="email"
      >
        <el-input
        v-model="accPersonAdd.email"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="6">
    </el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Место рождения" prop="place_birth">
        <el-input
        v-model="accPersonAdd.place_birth"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Место работы" prop="place_work">
        <el-input
        v-model="accPersonAdd.place_work"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

  <el-row class="row-bg" justify="space-between">
    <el-col :span="15">
      <el-form-item label="Должность" prop="post">
        <el-input
        v-model="accPersonAdd.post"
        ></el-input>
      </el-form-item>
    </el-col>
    <el-col :span="3"></el-col>
  </el-row>

<el-row>
  <el-col>
    <span class="download_link" @click="downloadFile">
      Скачать шаблон
      <el-icon><download /></el-icon>
      </span>
    <el-form-item label= "Загрузите файл согласия на передачу и обработку персональных данных сопровождающего лица" prop="photo">
    <el-upload
      ref="upload"
      drag
      action="#"
      :auto-upload="false"
      :on-change="handleChange"
      :limit="1"
      :on-exceed="handleExceed"
    >

    <el-icon class="el-icon--upload"><upload-filled /></el-icon>
    <div class="el-upload__text">
      Перетащите сюда файл или <em>кликните</em>
    </div>
    <template #tip>
      <div class="el-upload__tip">
        jpg/pdf файл не более 10мб
      </div>
    </template>
  </el-upload>
    </el-form-item>
  </el-col>
</el-row>

<el-row>
  <el-col>
    <el-button
      type="primary"
      plain
      @click="addAccPer(this.$refs.additionalAddRef)"
      style="marginTop: 20px"
      :loading="isLoadingPostReq"
          >Добавить</el-button
        >
  </el-col>
</el-row>
   </el-form>
  </div>



  <div class="btns">
    <el-form-item>
      <el-button v-if="!isMoscow" type="primary" @click="onSubmit(this.$refs.additionalRef, isMoscow)"
        >Далее</el-button
      >
      <el-button v-else type="primary" @click="onSubmit(this.$refs.additionalRef, isMoscow)"
        >Завершить регистрацию</el-button
      >
    </el-form-item>
    <el-button
      class="btns_prev"
      type="primary"
      plain
      @click="$emit('prevStep')"
          >Назад</el-button
        >
    </div>
  </el-form>
</template>

<script>
import { UploadFilled, Download } from '@element-plus/icons-vue'
import { jsPDF } from "jspdf"
import { mapGetters, mapActions } from 'vuex'
import { ElNotification } from 'element-plus'

export default {
  components: {
    UploadFilled,
    Download
  },
  emits: ['nextStep', 'prevStep'],
  data() {
    return {
      accPerson: {
        accompanying_persons: {
          list: []
        }
      },
      accPersonAdd: {
        "fio": "",
        "sex": "",
        "birthday": "",
        "place_birth": "",
        "phone": "",
        "email": "",
        "place_work": "",
        "post": "",
        "agree_file": '',
      },
      isAdd: false,
      searchFIO: '',
      searchPhone: '',
      searchEmail: '',
      pageSize: 20,
      currentPage: 1,
      rules: {
        person: [
          {
            validator: this.validatePerson,
            trigger: 'change'
          }
        ],
        fio: [
          {
            required: true,
            message: 'Укажите данные',
            trigger: 'blur',
          }
        ],
        sex: [
          {
            required: true,
            message: 'Укажите данные',
            trigger: 'blur',
          }
        ],
        birthday: [
          {
            required: true,
            message: 'Укажите данные',
            trigger: 'blur',
          },
          {
            validator: this.validateBirthday,
            trigger: 'change'
          }
        ],
        email: [
          {
            required: true,
            message: 'Введите email',
            trigger: 'blur',
          },
          {
            type: 'email',
            message: 'Email некорректный',
            trigger: 'blur',
          },
          {
            validator: this.validateEmail,
            trigger: 'blur'
          }
        ],
        phone: [
          { required: true,
            message: 'Введите номер телефона'
          },
          {
            validator: this.validatePhone,
            trigger: 'blur'
          }
        ],
        place_birth: [
          {
            required: true,
            message: 'Укажите данные',
            trigger: 'blur',
          }
        ],
        place_work: [
          {
            required: true,
            message: 'Укажите данные',
            trigger: 'blur',
          }
        ],
        post: [
          {
            required: true,
            message: 'Укажите данные',
            trigger: 'blur',
          }
        ],
        photo: [
          {
            validator: this.validatePhoto,
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    ...mapGetters(['accList', 'isLoadingPostReq', 'isLoadingBlock', 'isMoscow']),
    totalItems() {
      return this.filterTableData.length;
    },
    filterTableData() {
      const filterArr = this.accList.filter(
    (data) =>
      (!this.searchFIO ||
      data.fio.toLowerCase().includes(this.searchFIO.toLowerCase())) &&
      (!this.searchPhone ||
      data.phone.toLowerCase().includes(this.searchPhone.toLowerCase())) &&
      (!this.searchEmail ||
      data.email.toLowerCase().includes(this.searchEmail.toLowerCase()))
      )
      return filterArr
    },
    currentIndex() {
      return (this.currentPage - 1) * this.pageSize;
    }
  },

  created() {
    this.GET_ACC_PER();
  },

  methods: {
    ...mapActions([
      'GET_ACC_PER',
      'POST_ACC_PER',
      'SET_ACCOMP_PEOPLE',
      'POST_REGISTER',
      'POST_FILES',
      'GET_EMAIL',
      'GET_PHONE']),
    validatePerson(rule, value, callback) {
      if (!this.accPerson.accompanying_persons.list.length) {
        callback(new Error('Выберите сопровождающего'))
        } else {
          callback()
        }
    },
    validateBirthday(rule, value, callback) {
      const now = new Date();
      const birthday = new Date(value);
      if (now  < birthday || now.getDate() === birthday.getDate()) {
          callback(new Error('Некорректная дата'))
        } else {
          callback()
        }
    },
     addAccPer(formEl) {
       const formData = new FormData();
       formData.append('fio', this.accPersonAdd.fio);
       formData.append('sex', this.accPersonAdd.sex === 'Мужской' ? '1' : '2');
       formData.append('birthday', this.accPersonAdd.birthday);
       formData.append('place_birth', this.accPersonAdd.place_birth);
       formData.append('phone', this.accPersonAdd.phone);
       formData.append('email', this.accPersonAdd.email);
       formData.append('place_work', this.accPersonAdd.place_work);
       formData.append('post', this.accPersonAdd.post);
       formData.append('agree_file', this.accPersonAdd.agree_file);

        if (!formEl) return
       formEl.validate((valid) => {
      if (valid) {
        this.POST_ACC_PER(formData)
        .then((res) => {
          if(res.data.error_message) {
            const errors = JSON.parse(res.data.error_message);
            for (let error in errors) {
              ElNotification({
              title: 'Ошибка',
              message: `${errors[error]}`,
              type: 'Ошибка',
            })
            }
          } else {
            ElNotification({
              title: 'Готово!',
              message: `Новое сопровождающее лицо добавлено. Выберите его из списка`,
              type: 'success',
            })
            this.GET_ACC_PER();
          }
          this.isAdd = false;
        })
      } else {
        return false
    }
  })
    },
    handleAdd(index, row) {
      if (this.accPerson.accompanying_persons.list.indexOf(row.id) === -1)
      this.accPerson.accompanying_persons.list.push(row.id);
    },
    handleDelete(index, row) {
      const itemIndex = this.accPerson.accompanying_persons.list.indexOf(row.id);
      this.accPerson.accompanying_persons.list.splice(itemIndex, 1);
    },
    downloadFile() {
      const doc = new jsPDF();
      doc.text("Hello world!", 15, 15);
      doc.save('output.pdf')
    },
    validateEmail(rule, value, callback) {
      this.GET_EMAIL(value)
      .then((res) => {
        if (!res.data.success_message) {
          ElNotification({
              title: 'Ошибка',
              message: `${JSON.parse(res.data.error_message).email}`,
              type: 'error',
            })
          callback(new Error('Email уже существует'))
        } else {
          callback()
        }
      })
    },
    validatePhone(rule, value, callback) {
      this.GET_PHONE(value)
      .then((res) => {
        if (!res.data.success_message) {
          ElNotification({
              title: 'Ошибка',
              message: `${JSON.parse(res.data.error_message).phone}`,
              type: 'error',
            })
          callback(new Error('Телефон уже существует'))
        } else {
          callback()
        }
      })
    },
    validatePhoto(rule, value, callback) {
      const bytes = this.accPersonAdd.agree_file.size;
      const type = this.accPersonAdd.agree_file.type;
      if (!bytes) {
        callback(new Error('Загрузите файл'))
      }
      else if (!type.includes('jpeg') && !type.includes('jpg') && !type.includes('pdf')) {
        callback(new Error('Файл должен быть в формате jpg или pdf'))
      }
      else if (bytes / Math.pow(1000, 2) > 10) {
          callback(new Error('Файл должен быть не более 10мб'))
        } else {
          callback()
        }
    },
    handleExceed(files) {
      this.$refs.upload.clearFiles();
      this.$refs.upload.handleStart(files[0]);
    },
    handleChange(file) {
      this.accPersonAdd.agree_file = '';
      this.accPersonAdd.agree_file = file.raw;
    },
    onSubmit(formEl, isMoscow) {
          if (!formEl) return
       formEl.validate((valid) => {
      if (valid && this.accPerson.accompanying_persons.list.length) {
        const data = {
          list: this.accPerson.accompanying_persons.list
        }
        this.SET_ACCOMP_PEOPLE(data);
        if(isMoscow) {
          this.POST_REGISTER()
          .then((res) => {
          if(res.data.error_message) {
            const errors = JSON.parse(res.data.error_message);
            for (let error in errors) {
              ElNotification({
              title: 'Ошибка',
              message: `${errors[error]}`,
              type: 'error',
            })
            }
          } else {
            this.POST_FILES(res.data.hash).then(() => {
              ElNotification({
              title: 'Готово!',
              message: `Подтвердите почту`,
              type: 'success',
            })
            })
            this.$emit('nextStep');
          }
        });
        } else {
          this.$emit('nextStep');
        }
      } else {
        return false
    }
  })
    },
  }

}
</script>

<style>

</style>