<template>
<div>
  <HeaderForm />

  <main class="container">
    <el-row>
      <el-col :span="24">
        <StepsForm :step="step"
        @goTo = "goTo(item)"/>

        <PersonalData
          v-show="step===0"
          @nextStep="nextStep"
        />
        <SchoolForm
          v-show="step===1"
          @nextStep="nextStep"
          @prevStep="prevStep"
          />
        <OtherForm
          v-show="step===2"
          @nextStep="nextStep"
          @prevStep="prevStep"
        />
        <AccompanyingPerson
          v-show="step===3"
          @nextStep="nextStep"
          @prevStep="prevStep"
        />
        <RouteForm
          v-show="step===4"
          @nextStep="nextStep"
          @prevStep="prevStep"
        />
        <transition name="el-fade-in-linear">
        <FormEnd v-show="isEnd" />
        </transition>
      </el-col>
    </el-row>

  </main>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import HeaderForm from '../components/HeaderForm.vue'
import StepsForm from '../components/StepsForm.vue'
import PersonalData from '../components/PersonalData.vue'
import SchoolForm from '../components/SchoolForm.vue'
import OtherForm from '../components/OtherForm.vue'
import AccompanyingPerson from '../components/AccompanyingPerson.vue'
import RouteForm from '../components/RouteForm.vue'
import FormEnd from '../components/FormEnd.vue'



export default {
  components: {
    HeaderForm,
    StepsForm,
    PersonalData,
    SchoolForm,
    OtherForm,
    AccompanyingPerson,
    RouteForm,
    FormEnd
  },
  data() {
    return {
      step:  0
    }
  },
  computed: {
    ...mapGetters(['isMoscow']),
    isEnd() {
      return (this.step === 4 && this.isMoscow) || this.step === 5
    }
  },
  methods: {
    nextStep() {
      if (this.step < 5 ) {
        this.step += 1;
      }
    },
    prevStep() {
      if (this.step > 0) {
        this.step -= 1;
      }
    }
  }
}
</script>

